import { useEffect, useState } from "react";
import { Padding } from "./base";
import { GameActionButton, NextPageButton, NextRoundButton } from "./buttons";
import pd from "./img/pd.jpg";

import { gameHistory, saveGameRound, maxRound, onEndResponse } from "./data";

function getRandomAction() {
    return Math.random() < 0.5 ? "cooperate" : "defect";
}

export default function Game() {
    let [round, setRound] = useState(1);
    let [score, setScore] = useState(0);
    let [opponentAction, setOpponentAction] = useState("");
    let [yourAction, setYourAction] = useState("");
    let [roundScore, setRoundScore] = useState(0);
    let [lastAction, setLastAction] = useState("");

    useEffect((_) => {
        if (gameHistory.length != 0) {
            let lastRound = gameHistory[gameHistory.length - 1];
            setRound(lastRound.roundNumber + 1);
            setOpponentAction(lastRound.opponentAction);
            setYourAction(lastRound.playerAction);
            setScore(lastRound.totScore);
            setRoundScore(lastRound.roundScore);
        }
    }, []);

    function calcRoundScore(oppAction) {
        if (oppAction == "defect" && yourAction == "defect") {
            return 1;
        } else if (oppAction == "defect" && yourAction == "cooperate") {
            return 0;
        } else if (oppAction == "cooperate" && yourAction == "defect") {
            return 5;
        } else if (oppAction == "cooperate" && yourAction == "cooperate") {
            return 3;
        }
        return null;
    }

    function onNextRound() {
        if (yourAction == "") {
            return;
        }

        setLastAction(yourAction);
        let oppAction = getRandomAction();
        setOpponentAction(oppAction);

        let roundScore = calcRoundScore(oppAction);
        setScore(score + roundScore);
        setRoundScore(roundScore);

        setRound(round + 1);

        saveGameRound(round, yourAction, oppAction, roundScore, score + roundScore);
    }

    function toggleAction(action) {
        if (yourAction == action) {
            setYourAction("");
        } else {
            setYourAction(action);
        }
    }

    function nextButton() {
        if (round <= maxRound) {
            return (
                <NextRoundButton
                    action={onNextRound}
                    available={!(yourAction == "")}
                />
            );
        } else {
            return (
                <>
                    <Padding padding="1.5rem" />
                    <NextPageButton to="/bye" onClick={onEndResponse}>Done</NextPageButton>
                </>
            );
        }
    }

    return (
        <div className="content">
            <h2 className="content-element">
                Round {round}: what do you choose?
            </h2>
            {round != 1 && (
                <>
                    <h2
                        style={{
                            marginBottom: "1rem",
                            color: "#c21807",
                            fontSize: "1.2rem",
                        }}
                    >
                        {roundScore} points! Your score is now {score}.
                    </h2>
                    <p
                        className="content-element"
                        style={{
                            color: "#aaaaaa",
                        }}
                    >
                        Your opponent chose {opponentAction} and you chose{" "}
                        {lastAction}, so you get {roundScore} points.
                    </p>
                </>
            )}
            <img
                src={pd}
                alt="The prisoners dilemma"
                style={{
                    height: "12rem",
                    border: "1px solid black",
                    borderRadius: "0.5rem",
                    marginBottom: "3rem",
                    boxShadow: "1rem 1rem grey",
                }}
            />
            <GameActionButton
                selected={yourAction == "cooperate"}
                action={() => toggleAction("cooperate")}
            >
                Cooperate
            </GameActionButton>
            <GameActionButton
                selected={yourAction == "defect"}
                action={() => toggleAction("defect")}
            >
                Defect
            </GameActionButton>
            {nextButton()}
            <Padding padding="2rem" />
        </div>
    );
}
